        <img :src="image" class="mypics" alt="eme">
      </div>
      <div class="links">
        <div class="locale-lang">
          <form class="d-flex jc-center mb-30">
            <select v-model="selected" id="select-lang" class="locale-select" v-on:change="handleLocaleChange">
              <option value="fr">
                <img :src="france" class="locale-img locale-img-fr" alt="localeimg"> FR
              </option>
              <option value="en">
                <img :src="unitedstate" class="locale-img locale-img-us" alt="localeimg"> EN
              </option>
            </select>
          </form>
        </div>
        <ul class="nav-bar">
          <router-link to="/">{{ __('head.about') }}</router-link>
          <router-link to="/portfolio">{{ __('head.portfolio') }}</router-link>
          <router-link to="/educations">{{ __('head.education') }}</router-link>
          <router-link to="/competences">{{ __('head.comp') }}</router-link>
          <!-- <router-link to="/awards">{{ __('head.awards') }}</router-link> -->
          <router-link to="/autres" >{{ __('head.other') }}</router-link>
        </ul>
      </div>
    </header>
    <main>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
import image from './assets/dadapas.png';
import france from './assets/fr.svg';
import unitedstate from './assets/us.svg';
import { getLocale } from './locale/lang'
import { mapGetters } from 'vuex'

export default {
  name: 'App',
  methods: {
    handleLocaleChange(e) {
      this.$store.commit('setLocale', e.target.value)
      
      let langs = getLocale(this.$store.getters.getLocale)
      
      this.$store.commit('setCurrentLocale', langs)
    },
    isLang(locale) {
      console.log(document.querySelector('#select-lang'), locale)
    },
  },
  computed: {
    __() {

      return this.$store.getters.trans
    },
    ...mapGetters([])
  },
  data: function () {
    
    let langs = getLocale(this.$store.getters.getLocale)
    this.$store.commit('setCurrentLocale', langs)

    return {
      image,
      france,
      unitedstate,
      selected: this.$store.getters.getLocale
    }
  }
}
</script>

