import { createStore } from 'vuex'

const LOCAL_LANG = 'LOCAL_LANG'
const locale = localStorage.getItem(LOCAL_LANG)

const store = createStore({
	state: {
		locale: locale ? locale : 'fr',
		currentLocale: {}
	},
	getters: {
		getLocale(state) {
			return state.locale
		},
		getCurrentLocale(state) {
			return state.currentLocale
		},

		trans(state) {
			return function(keyword, dflt = undefined) {

				const current = state.currentLocale

				let data = current[keyword] ? current[keyword] : keyword      
				if (data == keyword && dflt != undefined)
					return dflt

				return data
			}
		}
	},
	mutations: {
		setLocale(state, lang) {
			console.log(localStorage.getItem(LOCAL_LANG))
			localStorage.setItem(LOCAL_LANG, lang)
			state.locale = lang
		},
		setCurrentLocale(state, localeLoaded) {
			state.currentLocale = localeLoaded
		}
	},
	actions: {
		/*setActionCurrentLocale({ commit, state })
		{
			let data = readFileSync(`../locale/${state.locale}.json`, 'utf8');
			console.log(data)
			commit('setCurrentLocale', JSON.parse(data))
		}*/
	}
})

export default store